@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding:wght@400;700&display=swap);
.book__data img {
  width: 100px;
  height: 130px;
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.5);
}

.book__data {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.book__data-rank {
  margin-right: 10px;
  color: black;
  font-weight: 700;
}

.book__data-title {
  color: white;
  font-size: small;
  margin-top: 7px;
  width: 100px;
}

.book a {
  text-decoration: none;
}

.book__data_img {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.container {
  margin: 40px 0;
  margin-left: 250px;
  margin-right: 50px;
}

.container .title_name {
  background-color: #192a56;
  color: white;
  border-radius: 5px;
  font-size: 20px;
  padding: 7px;
}

.loader {
  width: 100%;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  font-size: 30px;
}

.books {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 120px;
  text-align: center;
  height: 200px;
}

.bookshelf {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.bookshelf_one {
  margin-right: 30px;
}

.bookshelf_img {
  grid-column-start: 1;
  grid-column-end: 4;
  width: 100%;
  z-index: -1;
}

.about__container {
  background-color: #ffffff;
  border-radius: 5px;
  width: 50%;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  margin: 0 auto;
  margin-top: 100px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px rgba(0, 0, 0, 0.25);
  height: auto;
}

.about__container img {
  position: relative;
  top: -50px;
  left: 30px;
  width: 200px;
  height: 300px;

  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.25);
}

.books__data {
  margin-right: 30px;
  margin-bottom: 20px;
  font-size: 14px;
}

.book__priceStandard {
  text-decoration: line-through;
}

.books__data .books__title {
  font-size: x-large;
}

.book__description-title {
  font-size: 20px;
  border-bottom: 1px solid black;
  padding-bottom: 5px;
  margin-top: 20px;
}

.book__priceSales {
  color: red;
  font-weight: bold;
}

.book__sale {
  text-align: end;
}

.books__data a {
  text-decoration: none;
  text-align: end;
  color: #2d3436;
}
.book__sale span {
  background-color: #b2bec3;
  padding: 5px;
  border-radius: 5px;
  margin-left: 10px;
}

.book__description {
  margin-bottom: 30px;
  line-height: 19px;
}

.nav {
  z-index: 1;
  position: fixed;
  top: 50px;
  left: 50px;
  display: flex;
  flex-direction: column;
  background-color: #ecf0f1;
  padding: 10px 20px;
  border-radius: 5px;
}

.nav a {
  text-decoration: none;
  color: #2c3e50;
  text-align: center;
  font-weight: 600;
}

.nav a:not(:last-child) {
  margin-bottom: 20px;
}

body {
  background-color: #dfe6e9;
  font-family: "Nanum Gothic Coding", monospace;
}

